import React, { useState, useCallback, useEffect } from 'react';
import PropTypes from 'prop-types';
import { EventBanners } from './Style';
import eb_coupon from '../../assets/images/eb_coupon.png';
import useScroll from '../../hooks/useScroll';

const EventBanner = ({ isMobile }) => {
    const [show, setShow] = useState(true);
    const [close, setClose] = useState(false);
    const [scrollY] = useScroll();

    const onHide = useCallback(() => {
        setShow(false);
    }, []);

    const onClose = useCallback(() => {
        setClose(true);
        setShow(false);
    }, []);

    useEffect(() => {
        if (!isMobile) {
            if (scrollY > 100) {
                setShow(false);
            } else if (scrollY === 0) {
                setShow(true);
            }
        } else {
            if (!close) {
                setShow(true);
            }
        }
    }, [isMobile, scrollY, close]);

    return (
        <EventBanners className={show ? 'on' : ''}>
            <article className='eb-content'>
                <div className='eb-txt'>
                    <h3>
                          25   <br/>
                           1:1  <br/>
                          <strong>   </strong>
                    </h3>
                </div>
                <figure className='eb-img'>
                    <img src={eb_coupon} alt='' />
                </figure>
                <div className='eb-btn-wrap'>
                    <a href='https://www.coffeechat.kr/event' target='_blank' rel='noreferrer' title='   ' className='eb-btn'>   </a>
                </div>
                <div className='eb-btn-wrap'>
                    <button type='button' title=' ' className='eb-btn2' onClick={onClose}> </button>
                </div>
            </article>
            <button type='button' className='eb-close' title=' ' onClick={onHide}>
                <span className='hidden'> </span>
            </button>
        </EventBanners>
    );
};

EventBanner.propTypes = {
    isMobile: PropTypes.bool,
};

export default EventBanner;